
export const LOGIN = 'LOGIN'
export const REGISTER = 'REGISTER'
export const LOGOUT ='LOGOUT'
export const REQUESTDATA = 'REQUESTDATA'
export const UPDATEHEADLINE = 'UPDATEHEADLINE'
export const GOTOPROFILE = 'GOTOPROFILE'
export const GOTOMAIN = 'GOTOMAIN'
export const REQUESTUSERGROUP = 'REQUESTUSERGROUP'
export const REQUESTCOMMENT = 'REQUESTCOMMENT'
export const UPDATEAVATAR = 'UPDATEAVATAR'

export const smallimg = 'data:image/jpeg;base64,'
export const bigimg = 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcS0Y8KJxbBv4yxN4eoXfpdUnhqWXnEj_kpysQ&usqp=CAU'

export const register=(userId,baseinfo)=>{
    return{
        type:REGISTER,
        userId:userId,
        baseinfo:baseinfo
    }
}

export const userlogin=(userId,paasword)=>{
    return{
        type:LOGIN,
        userId:userId,
        password:paasword
    }
}

export const userlogout=()=>{
    return {
        type: LOGOUT
    }
}

export const requestdata=(data)=>{
    return {
        type: REQUESTDATA,
        data:data
    }
}

export const updateheadline=(headline)=>{
    return {
        type:UPDATEHEADLINE,
        headline:headline
    }
}

export const gotoprofile = (input)=>{
    return {
        type:GOTOPROFILE,
        input:input
    }
}

export const gotomain = (input)=>{
    return {
        type:GOTOMAIN,
        input:input
    }
}

export const requestusergroup = (usergroup)=>{
    return {
        type:REQUESTUSERGROUP,
        usergroup:usergroup
    }
}

export const requestcomment = (comments)=>{
    return {
        type:REQUESTCOMMENT,
        comments:comments
    }
}

export const updateavatar = (avatar)=>{
    return {
        type:UPDATEAVATAR,
        avatar:avatar
    }
}

